.step-text {
    font-size: 0.8em;
}

.selected {
    position: relative;
    overflow: hidden;
    border-radius: 6px;
}

.selected::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 25px;
    background-color: #123;
}

.selected::before {
    content: 'Selected';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    color: #ffff;
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    z-index: 2;
}

.pick {
    cursor: pointer;
}

.pick:hover {
    opacity: 0.5;
}

.result {
    width: 294px;
    height: 607px;
    position: relative;
}

.result img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.result .content {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.result .name,
.result .txt {
    color: #fff;
    /* font-family: 'Vazirmatn', sans-serif; */
    font-family: 'Vazirmatn', sans-serif;
    /* font-family: 'Markazi Text', serif; */
    font-weight: 400;
}

.result .name {
    font-size: 1.5em;
}

.result .txt {
    font-size: 1.4em;
}

.darker {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: rgba(0, 0, 0, .3);
}